/* Section with background image */
.sec-with-imgbg,
.sec-with-imgbg > div:first-child {
  position: relative;
}

.sec-with-imgbg > div:first-child {
  z-index: 9;
}

.sec-imgbg {
  -o-object-fit: cover;
  object-fit: cover;
  height: 100%;
  top: 0;
  z-index: 1;
}

.welcome-imgbg,
.sec-rating-imgbg {
  width: auto;
  height: auto;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 110%;
  display: block;
}

@media (min-width: 768px) {
  .slantbg {
    -webkit-transform: skewY(-2.73deg);
    -ms-transform: skewY(-2.73deg);
    transform: skewY(-2.73deg);
    overflow: hidden;
  }
}